import { useState } from "react";
import Image from "next/image";
import { motion } from "framer-motion";

const winners = [
  { name: "თემო კახიძე", amount: "$150", date: "2 თებერვალი 2025", img: "/winners/winner1.jpg" },
  { name: "ნიკა მარგველაშვილი", amount: "$200", date: "9 თებერვალი 2025", img: "/winners/winner2.jpg" },
  { name: "სოფო ბერიძე", amount: "$300", date: "16 თებერვალი 2025", img: "/winners/winner3.jpg" },
  { name: "გიორგი მაისურაძე", amount: "$500", date: "23 თებერვალი 2025", img: "/winners/winner4.jpg" },
  { name: "თემო კახიძე", amount: "$150", date: "2 თებერვალი 2025", img: "/winners/winner1.jpg" },
  { name: "ნიკა მარგველაშვილი", amount: "$200", date: "9 თებერვალი 2025", img: "/winners/winner2.jpg" },
  { name: "სოფო ბერიძე", amount: "$300", date: "16 თებერვალი 2025", img: "/winners/winner3.jpg" },
  { name: "გიორგი მაისურაძე", amount: "$500", date: "23 თებერვალი 2025", img: "/winners/winner4.jpg" },
];

export default function WinnersPage() {
  const [activeIndex, setActiveIndex] = useState(0);

  const nextWinner = () => setActiveIndex((prev) => (prev + 1) % winners.length);
  const prevWinner = () => setActiveIndex((prev) => (prev - 1 + winners.length) % winners.length);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-900 text-white">
      <h1 className="text-4xl font-bold mb-8">🏆 გამარჯვებულები</h1>
      
      <div className="relative w-full flex justify-center items-center">
        {/* მარცხენა ღილაკი */}
        <button onClick={prevWinner} className="absolute left-4 text-4xl p-2 bg-gray-700 rounded-full hover:bg-gray-600 transition">
          ⬅️
        </button>

        {/* 🏆 **3D წრიული ანიმაცია** */}
        <div className="relative w-[400px] h-[400px] flex items-center justify-center">
          {winners.map((winner, index) => {
            const offset = (index - activeIndex + winners.length) % winners.length; 
            const scale = offset === 0 ? 1.2 : 0.8; 
            const opacity = offset === 0 ? 1 : 0.5;
            const zIndex = offset === 0 ? 10 : 5;
            
            return (
              <motion.div
                key={index}
                className="absolute w-[180px] h-[180px] rounded-full overflow-hidden border-4 border-yellow-400 shadow-lg"
                animate={{
                  scale,
                  opacity,
                  x: offset === 1 ? 200 : offset === 3 ? -200 : 0,
                  y: offset === 1 || offset === 3 ? 50 : 0,
                  rotateY: offset * 30,
                  zIndex
                }}
                transition={{ duration: 0.5 }}
              >
                <Image
                  src={winner.img}
                  alt={winner.name}
                  layout="fill"
                  objectFit="cover"
                  className="rounded-full"
                />
              </motion.div>
            );
          })}
        </div>

        {/* მარჯვენა ღილაკი */}
        <button onClick={nextWinner} className="absolute right-4 text-4xl p-2 bg-gray-700 rounded-full hover:bg-gray-600 transition">
          ➡️
        </button>
      </div>

      {/* 🏅 **გამარჯვებულის აღწერა** */}
      <div className="text-center mt-6 bg-gray-800 p-4 rounded-lg shadow-lg">
        <h2 className="text-3xl font-bold text-yellow-400">{winners[activeIndex].name}</h2>
        <p className="text-lg text-gray-300 mt-2">💰 {winners[activeIndex].amount}</p>
        <p className="text-md text-gray-400">📅 {winners[activeIndex].date}</p>
      </div>
    </div>
  );
}
